/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                font-size: 20px;
                line-height: inherit;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {
                height: 21px;
                
                svg {
                    width: 25px;

                    path, rect {
                        stroke-width: 0.2;
                    }
                }

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            max-height: 100vh;
            pointer-events: auto;
            @include qodefTransition(1s);

            ul {
                opacity: 1;
                @include qodefTransition(.3s .1s);
            }
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 280px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    max-height: 0;
    box-shadow: $menu-drop-down-box-shadow;
    box-sizing: border-box;
    pointer-events: none;
    @include qodefTransition(.3s);

    ul {
        margin: 25px;
        padding: 0;
        opacity: 0;
        @include qodefTransition(.2s);

        li {
            @include qodefTableLayout();
            padding: 12px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 72px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        vertical-align: top;
        padding-left: 15px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 20px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: block;
            margin: 3px 0 0;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            font-weight: 500;
            color: $default-heading-color;
        }
    }

    .qodef-cart-bottom {
        position: relative;
        padding: 20px 0 0;
        margin-top: 10px;
        
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: -25px;
            width: calc(100% + 50px);
            height: 1px;
            background-color: $additional-border-color;
        }

        .qodef-subtotal-holder {
            @include qodefFlexBoxLayout(relative, center, space-between);
            margin: 0 0 21px;
            
            > * {
                display: block;
                width: 100%;
            }
            
            .qodef-total-amount {
                text-align: right;
                white-space: nowrap;
            }
        }

        .qodef-view-cart {
            @include qodefButtonDefaultStyle();
            @include qodefButtonSize(huge);
            @include qodefButtonSolidColor();

            &:hover {
                @include qodefButtonSolidHoverColor();
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */