/* Woocommerce Product Page shortcode style - begin
========================================================================== */

div.woocommerce {

    > .single-product {

        .images {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0;
            padding: 0;
    
            figure {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 -13px;
        
                a {
                    @include qodefRelativeHolderLayout();
            
                    img {
                        position: relative;
                        width: 100%;
                        display: block;
                    }
                }
            }
    
            .woocommerce-product-gallery__trigger {
                position: absolute;
                top: 10px;
                right: 10px;
                width: 30px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                z-index: 10;
            }
    
            .woocommerce-product-gallery__image {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 13px;
                box-sizing: border-box;
        
                &:first-child {
                    left: 13px;
                    width: calc(100% - 26px);
                    padding: 0;
                }
        
                .zoomImg {
                    cursor: crosshair;
                }
            }
    
            &.woocommerce-product-gallery--with-images {
        
                .woocommerce-product-gallery__image {
            
                    &:not(:first-child) {
                        float: left;
                        padding-top: 26px;
                    }
                }
            }
    
            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);
            
                    .woocommerce-product-gallery__image {
                
                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
        }
    
        .woocommerce-tabs {
            @include qodefRelativeHolderLayout();
            margin: 72px 0 0;
            background-color: #fff;
            box-shadow: $default-box-shadow;
        
            ul.tabs {
                @include qodefRelativeHolderLayout();
                margin: 0;
                padding: 0;
                list-style: none;
                background-color: $additional-background-color;
                clear: both;
            
                > li {
                    float: left;
                    margin: 0;
                    padding: 0;
                
                    &.active {
                    
                        a {
                            color: $default-heading-color;
                            background-color: #fff;
                        }
                    }
                
                    a {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                        padding: 14px 36px;
                        font-size: 13px;
                        line-height: 30px;
                        font-weight: 600;
                        letter-spacing: .1em;
                        text-transform: uppercase;
                        box-sizing: border-box;
                        @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
                    
                        &:hover {
                            color: $default-heading-color;
                            background-color: #fff;
                        }
                    }
                }
            }
        
            .entry-content {
                @include qodefRelativeHolderLayout(top);
                padding: 41px 36px 49px;
                box-sizing: border-box;
	            
	            @include phone-landscape {
		            padding-left: 15px;
		            padding-right: 15px;
	            }
            
                &.woocommerce-Tabs-panel--additional_information {
                    padding-top: 38px;
                    padding-bottom: 46px;
                }
            
                > h2 {
                    display: none;
                }
            
                > p {
                    margin: 0;
                }
            }
        
            table {
            
                tr, th {
                    border: 0;
                }
            
                td, th {
                    padding: 3px 0;
                    vertical-align: middle;
                    text-align: left;
                
                    p {
                        margin: 0;
                    }
                }
            
                th {
                    color: $default-heading-color;
                    width: 1%;
                    padding: 0 10px 0 0;
                }
            
                td {
                    width: 99%;
                }
            }
        
            #reviews {
                @include qodefRelativeHolderLayout();
            
                h2 {
                    margin: 0 0 23px;
                    @include qodefH4();
                }
            
                ol.commentlist {
                    list-style: none;
                
                    li {
                        margin: 0 0 15px;
                    
                        &:last-child {
                            margin: 0;
                        }
                    }
                
                    .comment_container {
                        @include qodefRelativeHolderLayout();
                    
                        img {
                            float: left;
                            width: 70px;
                        }
                    }
                
                    .comment-text {
	                    padding: 0 0 0 128px;
	                    min-height: 84px;
                    
                        p {
                            margin: 0;
                        }
	                    
	                    .meta {
		                    margin: 0 0 8px;
	                    }
                    }
                
                    .woocommerce-review__author {
	                    @include qodefH6();
                        color: $default-heading-color;
                    }
	
	                .woocommerce-review__published-date {
		                line-height: 1.428571428571429em;
		                font-weight: 600;
		                color: $default-heading-color;
	                }
                }
            
                .comment-respond {
                    margin-top: 39px;
                
                    .comment-reply-title {
                        display: block;
                        @include qodefHeadingStyle(h4);
                    }
                
                    .comment-form {
                        @include qodefRelativeHolderLayout(top);
                        margin: 22px 0 0;
                        padding: 44px 40px 55px;
                        background-color: $first-main-color;
                        clear: both;
                        box-sizing: border-box;
	
	                    @include phone-landscape {
		                    padding-left: 15px;
		                    padding-right: 15px;
	                    }
                    
                        input[type="submit"] {
                            width: 100%;
                            padding-left: 20px;
                            padding-right: 20px;
                            color: $default-heading-color;
                            background-color: #fff;
                            text-align: center;
                            border-radius: 0;
                            -webkit-border-radius:0;
                            -webkit-appearance: none;
                        
                            &:hover {
                                color: #fff;
                                background-color: rgba(#fff,.25);
                            }
                        }
                    }
                
                    p {
                        margin: 0;
                        color: #fff;
                    }
                
                    .comment-form-rating {
	                    margin: 20px 0;
                        color: #fff;
                    }
                
                    .stars {
                        overflow: hidden;
                    
                        a {
                            position: relative;
                            display: inline-block;
                            color: #fff;
                            font-size: 0;
                            margin: 0;
                            @include qodefWooCommerceSingleProductRatingsStyle();
                        
                            &:before,
                            &:after {
                                color: inherit !important;
                            }
                        
                            &:before {
                                z-index: auto;
                            }
                        }
                    }
                }
            }
        }
    
        .related.products,
        .upsells.products {
            margin: 109px 0 0;
        
            > h2 {
                display: block;
                margin: 0 0 47px;
                @include qodefH3();
            }
        
            .product {
            
                .qodef-pl-text-wrapper {
                    margin-bottom: 0;
                }
            }
        }
    }
}

/* Woocommerce Product Page shortcode style - end
========================================================================== */