/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-heading-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            box-sizing: border-box;
        }

        .qodef-quantity-input {
            position: relative;
            height: 60px;
            width: 58px;
            padding: 0;
            margin: 0 36px 0 0;
            font-family: inherit;
	        font-size: 16px;
	        font-weight: 500;
	        border-radius: 0;
            outline: 0;
	        
	        &:focus {
		        color: $first-main-color;
	        }
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            right: 0;
            width: 36px;
            height: 30px;
            font-size: 22px;
            line-height: 26px;
            border-left: 0;
            cursor: pointer;
            @include qodefTransition(color .2s ease-out);

            &:hover {
                color: $first-main-color;
            }
	        
	        &:before {
		        display: block;
		        line-height: inherit;
	        }
        }

        .qodef-quantity-plus {
            top: 0;
	        padding-top: 2px;
            border-bottom: 0;
        }

        .qodef-quantity-minus {
            bottom: 0;
	        border-top: 0;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            line-height: 28px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */