/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();
	
	&.qodef-info-on-image {
		
		.qodef-pli {
			
			.qodef-pli-title {
				margin-bottom: 5px;
				letter-spacing: -.05em;
			}
			
			.qodef-pli-rating-holder {
				margin: 0 0 11px;
			}
			
			.qodef-pli-price {
				margin: 0;
				letter-spacing: -.05em;
			}
			
			.qodef-pli-add-to-cart {
				margin: 28px 0 0;
			}
		}
		
		.qodef-pli-inner {
			
			.qodef-pli-text {
				padding: 0;
				background-color: $additional-background-color;
			}
			
			.qodef-pli-text-inner {
				
				.qodef-pli-add-to-cart {
					position: absolute;
					width: 100%;
					bottom: 0;
					left: 0;
					
					.button {
						width: 100%;
					}
				}
			}
		}
	}

    .qodef-pli {
        padding: 0;
        z-index: 100;

	    .qodef-pli-title {
		    margin: 0 0 10px;
	    }
	
	    .qodef-pli-category,
        .qodef-pli-excerpt {
            margin: 8px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout(top);
		    line-height: 1;
		    margin: 1px 0 3px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }
	
	    .qodef-pli-price {
		    @include qodefWooCommercePriceStyle(false, 8px 0 0);
	    }
	
	    .qodef-pli-info-holder {
		    @include qodefFlexBoxLayout(relative, flex-start, space-between);
		    line-height: 1;
		
		    .qodef-pli-price {
			    margin: 0;
		    }
		
		    .qodef-pli-rating-holder {
			    width: auto;
			    margin: 0;
		    }
	    }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        &:hover {

            .qodef-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
            @include qodefTransition(opacity .2s ease-out);
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

	        .qodef-pli-add-to-cart {

		        &.qodef-default-skin {

			        .button,
			        .added_to_cart {
				        @include qodefButtonSolidColor();

				        &:hover {
				            @include qodefButtonSolidHoverColor();
				        }
			        }
		        }

		        &.qodef-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.qodef-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include qodefButtonDefaultStyle();
	            @include qodefButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
	    margin: 21px 0 42px;
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */