/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    ul {
        
        li {
            padding: 6px 0;
	        margin: 0;
    
            a {
	            font-size: 14px;
	            line-height: 1.428571428571429em;
	            font-weight: 600;
        
                &:hover {
                    color: $first-main-color;
                }
            }
    
            ul {
                margin: 6px 0 0;
                padding: 0 0 0 10px;
        
                li {
            
                    &:last-child {
	                    padding-bottom: 0;
                    }
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .qodef-widget-title {
            margin-bottom: 32px;
        }
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    position: relative;
                    
                    a:not(.remove) {
                        @include qodefFlexBoxLayout(relative, center);
                    }
                    
                    img {
                        width: 72px;
                        margin-right: 15px;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        z-index: 2;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'ElegantIcons';
                            content: "\4d";
                            font-size: 18px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 9px 0 0;
            }
            
            .buttons {
                @include qodefFlexBoxLayout(relative, center, space-between);
                margin: 17px 0 0;
                
                a {
                    padding: 12px 28px 8px;
                }
            }
        }
    }
    
    &.widget_layered_nav {
        
        ul {
            
            li {
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
	    
	    .woocommerce-widget-layered-nav-dropdown {
		    padding-top: 15px;
	    }
    }
	
    &.widget_product_categories {
        margin-bottom: 32px;
    
        .qodef-widget-title {
            margin-bottom: 17px;
        }
        
        .dropdown_product_cat {
	        width: 100%;
	        max-width: 100%;
	        height: 50px;
	        padding: 0 20px;
	        margin-top: 15px;
	        font-family: inherit;
	        font-size: inherit;
	        line-height: 50px;
	        font-style: inherit;
	        font-weight: inherit;
	        color: $additional-text-color;
	        background-color: $additional-background-color;
	        border: 0;
	        border-radius: 0;
	        outline: none;
	        box-sizing: border-box;
        }
    }
    
    &.widget_price_filter {
        
        .qodef-widget-title {
            margin: 0 0 32px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 22px;
                background-color: #f0ede8;
	            overflow: hidden;
	            
                .ui-slider-handle {
                    position: absolute;
                    top: 0;
	                left: 0;
                    height: 22px;
                    width: 22px;
                    background-color: $second-main-color;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
	                
	                &:last-of-type {
		                @include qodefTransform(translateX(-22px));
	                }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            @include qodefFlexContainer(center, space-between, row-reverse);
            margin: 25px 0 0;
	        
	        .price_label {
		        color: $default-heading-color;
	        }
	        
	        .button {
		        padding: 0 !important;
		        font-family: inherit;
		        font-size: 14px;
		        font-weight: 600;
		        letter-spacing: -.03em;
		        text-transform: lowercase;
		        color: $first-main-color;
		        background-color: transparent;
		        border: 0;
		        
		        &:hover {
			        color: $first-main-color;
			        background-color: transparent;
            
                    &:after {
                        width: 100%;
                    }
		        }
		        
		        &:after {
			        content: '';
			        position: absolute;
			        left: 0;
			        bottom: 1px;
			        width: 0;
			        height: 2px;
			        background-color: currentColor;
                    @include qodefTransition(width .3s ease-out);
		        }
	        }
            
            .clear {
                display: none;
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products  {
        
        .qodef-widget-title {
            margin-bottom: 32px;
        }
        
        ul {
            
            li {
	            padding: 0;
                margin-bottom: 40px;
                
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        
        .qodef-woo-widget-img {
            display: table-cell;
            vertical-align: middle;
            padding-right: 15px;
            overflow: hidden;
            
            a, img {
                position: relative;
                display: block;
            }
            
            img {
                width: 72px;
            }
        }
        
        .qodef-woo-widget-content {
            display: table-cell;
            vertical-align: middle;
        }
        
        .qodef-woo-widget-title {
            display: block;
            margin: 0;
            
            a {
                color: inherit;
                
                &:hover {
                    color: $first-main-color;
                }
            }
        }
	
	    .qodef-woo-widget-price {
		    @include qodefWooCommercePriceStyle(false, 9px 0 0);
	    }
        
        .star-rating {
            margin-top: 8px;
        }
    }
    
    &.widget_recent_reviews {
        
        .qodef-widget-title {
            margin-bottom: 32px;
        }
        
        ul {
            
            li {
                min-height: 72px;
	            padding: 0;
                margin-bottom: 40px;
                
                &:last-child {
                    margin-bottom: 0;
                }
                
                a {
                    position: relative;
                    display: block;
                    padding: 0 0 0 87px;
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 72px;
                    }
                }
                
                > *:not(a) {
                    margin: 5px 0 0 87px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                .reviewer {
                    display: block;
                    margin-top: 6px;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
    
        .qodef-widget-title {
            margin-bottom: 32px;
        }
        
        .tagcloud {
    
            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0 6px 6px 0;
                padding: 10px 24px;
                @include qodefHeadingStyle(h6, false);
                font-size: 14px !important;
                text-transform: lowercase;
                color: #fff;
                background-color: $first-main-color;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
        
                &:hover {
                    background-color: $second-main-color;
                }
        
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }
    
    &.widget_product_search {
    
        .qodef-widget-title {
            margin-bottom: 32px;
        }
        
        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
    
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                height: 50px;
                font-family: inherit;
                font-size: inherit;
                line-height: 30px;
                font-weight: inherit;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out);
            }
    
            input {
                width: calc(100% - 50px);
                padding: 9px 0 11px 21px;
	            font-family: $default-text-font;
                color: $additional-text-color;
                background-color: $additional-background-color;
        
                @include qodefPlaceholder {
                    color: $additional-text-color;
                }
            }
    
            button {
                width: 50px;
                padding: 10px 0;
                text-align: center;
                color: #fff;
                background-color: $first-main-color;
                cursor: pointer;
                @include qodefTransition(background-color .2s ease-out);
        
                &:hover {
                    background-color: $second-main-color;
                }
        
                span {
                    display: block;
                    line-height: inherit;
            
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */