/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.qodef-woocommerce-page {

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 20px 30px;
        margin: 0 0 30px;
        line-height: 52px;
        list-style: none;
	    color: #fff;
        background-color: $first-main-color;

        > a {
	        color: #fff;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    .woocommerce-info {

        .showcoupon {
	        color: #fff;

            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */