/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: -10px;
    right: -10px;
    width: 68px;
    height: 68px;
    margin: 0;
    padding: 10px;
	font-family: $custom-text-font;
	font-size: 25px;
	line-height: 48px;
    text-align: center;
    color: $first-main-color;
    background-color: #fff;
    border-radius: 100%;
    box-shadow: $default-box-shadow;
    z-index: 100;
    box-sizing: border-box;
    @include qodefTransform(translateZ(0));
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle($single-style: false, $margin: 15px 0 0) {
    position: relative;
    display: block;
    margin: $margin;
    line-height: 1;
    letter-spacing: .05em;
    color: $default-heading-color;
    
    @if($single-style) {
        font-size: 22px;
        font-weight: 600;
    } @else {
        font-size: 14px;
        font-weight: 500;
    }
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: #d4d3d3;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 13px;
    line-height: 1;
    letter-spacing: 7px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        max-width: calc(100% - 2px);
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 5px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */