/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSolidColor();
        font-family: $default-text-font;
        cursor: pointer;
        z-index: 3;
    
        &:hover {
            @include qodefButtonSolidHoverColor();
        }
    }
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit):not(.single_add_to_cart_button) {
        @include qodefButtonSize();
    }
	
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonSize();
        color: $default-heading-color;
        background-color: #fff;
    }
    
    button.single_add_to_cart_button {
	    padding: 18px 42px;
    }
    
    a.button.added {
        display: none;
    }
    
    .woocommerce-message,
    .woocommerce-info {
        
        a.button {
            color: $default-heading-color;
            background-color: #fff;
    
            &:hover {
                @include qodefButtonSolidHoverColor();
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */