/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
	
	&.qodef-page-content-is-boxed {
		
		.qodef-content {
			
			.qodef-content-inner {
				
				> .qodef-container {
					
					> .qodef-container-inner {
						padding-bottom: 58px;
					}
				}
			}
			
			@include phone-landscape {
				
				.qodef-quantity-buttons {
					margin-bottom: 10px;
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 20px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -13px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 13px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 13px;
				    width: calc(100% - 26px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 26px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 20px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 25px 0 0;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle(true);
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 15px 0 0;
		    
		    .woocommerce-review-link {
			    margin-left: 2px;
			    vertical-align: middle;
			    color: $default-text-color;
			    
			    &:hover {
				    color: $first-main-color;
			    }
			    
			    @include phone-portrait {
				    display: block;
				    margin: 15px 0 0;
			    }
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 44px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout(top);
		    margin: 46px 0 0;
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 13px 0 0;
		    vertical-align: top;
	    }
	
	    .product_meta {
		    margin: 47px 0 0;
		
		    > span {
			    display: block;
			    margin: 0 0 16px;
			    font-size: 14px;
				font-weight: 600;
			    color: $default-heading-color;
			    
			    span {
				    
				    &.qodef-meta-label {
					    display: inline-block;
					    vertical-align: middle;
					    min-width: 122px;
				    }
				    
				    &:not(.qodef-meta-label) {
					    font-size: 14px;
					    font-weight: 400;
					    color: inherit;
				    }
			    }
			
			    a {
				    font-size: 14px;
				    font-weight: 400;
				    color: inherit;
				    
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    .qodef-social-title {
			    margin: 0;
			    min-width: 116px;
			    font-size: 14px;
			    font-weight: 600;
			    color: $default-heading-color;
		    }
		    
		    ul {
			    vertical-align: middle;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $first-main-color;
		    font-size: 13px;
		    font-weight: 600;
		    letter-spacing: .1em;
		    text-transform: uppercase;
		    margin: 18px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 72px 0 0;
		background-color: #fff;
		box-shadow: $default-box-shadow;
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			margin: 0;
			padding: 0;
			list-style: none;
			background-color: $additional-background-color;
			clear: both;
			
			> li {
				float: left;
				margin: 0;
				padding: 0;
				
				&.active {
					
					a {
						color: $default-heading-color;
						background-color: #fff;
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 14px 36px;
					font-size: 13px;
					line-height: 30px;
					font-weight: 600;
					letter-spacing: .1em;
					text-transform: uppercase;
					box-sizing: border-box;
					@include qodefTransition(color .2s ease-out, background-color .2s ease-out);
					
					&:hover {
						color: $default-heading-color;
						background-color: #fff;
					}
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout(top);
			padding: 41px 36px 49px;
			box-sizing: border-box;
			
			@include phone-landscape {
				padding-left: 15px;
				padding-right: 15px;
			}
			
			&.woocommerce-Tabs-panel--additional_information {
				padding-top: 38px;
				padding-bottom: 46px;
			}
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 0;
			}
		}
		
		table {
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				font-size: 14px;
				font-weight: 600;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			
			h2 {
				margin: 0 0 23px;
				@include qodefH4();
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 84px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 128px;
					min-height: 84px;
					
					@include phone-landscape {
						padding: 0;
						clear: both;
					}
					
					p {
						margin: 0;
					}
					
					.meta {
						margin: 0 0 8px;
					}
				}
				
				.woocommerce-review__author {
					@include qodefH6();
					color: $default-heading-color;
				}
				
				.woocommerce-review__published-date {
					line-height: 1.428571428571429em;
					font-weight: 600;
					color: $default-heading-color;
				}
			}
			
			.comment-respond {
				margin-top: 39px;
				
				.comment-reply-title {
					display: block;
					@include qodefHeadingStyle(h4);
				}
				
				.comment-form {
					@include qodefRelativeHolderLayout(top);
					margin: 22px 0 0;
					padding: 44px 40px 55px;
					background-color: $first-main-color;
					clear: both;
					box-sizing: border-box;
					
					@include phone-landscape {
						padding-left: 15px;
						padding-right: 15px;
					}
					
					input[type="submit"] {
						width: 100%;
						padding-left: 20px;
						padding-right: 20px;
						font-family: $default-text-font;
						color: $default-heading-color;
						background-color: #fff;
						text-align: center;
						border-radius: 0;
						-webkit-border-radius:0;
						-webkit-appearance: none;
						
						&:hover {
							color: #fff;
							background-color: $solid-button-hover-bg-color;
						}
					}
				}
				
				p {
					margin: 0;
					color: #fff;
				}
				
				.comment-form-rating {
					margin: 20px 0;
					color: #fff;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: #fff;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();
						
						&:before,
						&:after {
							color: inherit !important;
						}
						
						&:before {
							z-index: auto;
						}
					}
					
					@supports (-ms-ime-align: auto) {
						
						a {
							&.active:after {
								width: 100px;
								bottom: 6px;
							}
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 109px 0 0;
		
		> h2 {
			display: block;
			margin: 0 0 47px;
			@include qodefH3();
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */